/** @jsx jsx */
import * as React from "react";
import { Themed } from "@theme-ui/mdx";
import { css, jsx } from "theme-ui";
import { StaticImage } from "gatsby-plugin-image";

const IndexPage = () => {
  return (
    <Themed.root
      css={css({
        display: `flex`,
        flexDirection: `column`,
        alignItems: `center`,
        justifyContent: `center`,
        minHeight: `100vh`,
      })}
    >
      <div css={css({ width: `100%` })}>
        {/* <Themed.h1 css={css({ textAlign: `center`, color: `black` })}>
          Strona w budowie
        </Themed.h1> */}
        <StaticImage
          src="../images/splash_icon2.png"
          placeholder="tracedSVG"
          alt="Logo"
          style={{
            maxHeight: 200,
            maxWidth: 200,
            margin: `auto`,
            quality: 100,
          }}
          layout="fullWidth"
        />
      </div>
      {/* <Link to="/polityka-prywatnosci">Polityka prywatno≈õci</Link> */}
    </Themed.root>
  );
};

export default IndexPage;
